.header .navbar .nav__item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  width: 660px;
}

.header .navbar .nav__link {
  display: inline-block;
  font-family: 'Lato', sans-serif;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 0.5px;
  color: #fff;
  position: relative;
  -webkit-transition: .4s all ease;
  -o-transition: .4s all ease;
  transition: .4s all ease;
  padding: 5px;
  text-align: center;
}

.header .navbar .nav__link::after {
  content: "";
  width: calc(100% - 5px);
  height: 3px;
  background: #FA0102;
  position: absolute;
  bottom: 0px;
  left: 50%;
  -webkit-transform: scaleX(0) translateX(-50%);
  -ms-transform: scaleX(0) translateX(-50%);
  transform: scaleX(0) translateX(-50%);
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  -o-transition: transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

.header .navbar .nav__link:hover {
  color: #FA0102;
}

.header .navbar .nav__link:hover::after {
  -webkit-transform: scaleX(1) translateX(-50%);
  -ms-transform: scaleX(1) translateX(-50%);
  transform: scaleX(1) translateX(-50%);
}

.header .navbar .burger {
  display: none;
  height: 3em;
  width: 3em;
  position: relative;
  font-size: 12px;
  cursor: pointer;
  -webkit-transition: .2s all;
  -o-transition: .2s all;
  transition: .2s all;
  z-index: 99;
}

.header .navbar .burger .burger-lines:after {
  left: 0;
  top: -1em;
}

.header .navbar .burger .burger-lines:before {
  left: 1em;
  top: 1em;
}

.header .navbar .burger:after {
  content: '';
  display: block;
  position: absolute;
  height: 150%;
  width: 150%;
  top: -25%;
  left: -25%;
}

.header .navbar .burger .burger-lines {
  top: 50%;
  margin-top: -0.125em;
}

.header .navbar .burger .burger-lines,
.header .navbar .burger .burger-lines:after,
.header .navbar .burger .burger-lines:before {
  pointer-events: none;
  display: block;
  content: '';
  width: 100%;
  border-radius: 0.25em;
  background-color: white;
  height: 0.25em;
  position: absolute;
  -webkit-transform: rotate(0);
  -ms-transform: rotate(0);
  transform: rotate(0);
}

.header .navbar .burger .burger-lines:after {
  left: 0;
  top: -1em;
}

.header .navbar .burger .burger-lines:before {
  left: 1em;
  top: 1em;
}

.header .navbar .burger.burger-slip .burger-lines:after,
.header .navbar .burger.burger-slip .burger-lines:before {
  width: 2em;
}

.header .navbar .burger.burger-slip .burger-lines,
.header .navbar .burger.burger-slip .burger-lines:after,
.header .navbar .burger.burger-slip .burger-lines:before {
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

.header .navbar .burger.burger-slip.open .burger-lines {
  -webkit-transform: translateX(-2em);
  -ms-transform: translateX(-2em);
  transform: translateX(-2em);
  background-color: transparent;
}

.header .navbar .burger.burger-slip.open .burger-lines:before,
.header .navbar .burger.burger-slip.open .burger-lines:after {
  left: 0.5em;
  top: 0px;
}

.header .navbar .burger.burger-slip.open .burger-lines:before {
  -webkit-transform: translateX(2em) rotate(135deg);
  -ms-transform: translateX(2em) rotate(135deg);
  transform: translateX(2em) rotate(135deg);
}

.header .navbar .burger.burger-slip.open .burger-lines:after {
  -webkit-transform: translateX(2em) rotate(-135deg);
  -ms-transform: translateX(2em) rotate(-135deg);
  transform: translateX(2em) rotate(-135deg);
}

